.image-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 0 10px;

  grid-auto-rows: 10px;
}
.image-list img {
  width: 250px;
  grid-row-end: span 2;
}
@media only screen and (max-width: 600px) {
  .image-list div {
    justify-self: center;
  }
  .image-list img {
    min-width: 30rem;
  }
}



